.btnCot{
display:flex;
justify-content:center;
}
.btnCot button{
    background-color:#111;
}
.cotheader{
    padding-bottom:20px;
    color:rgb(255, 208, 0);
}
.form-inline {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
  }
  .form-inline input {
    width: 100%;
    vertical-align: middle;
    margin: 5px 10px 5px 0;
    padding: 10px;
    background-color: #fff;
    border: none;
    border-bottom: 2px solid #111;
    background: transparent;
    outline: none;
  }
  #inputGenero {
    width: 100%;
    border: none;
    border-bottom: 2px solid #111;
    border-radius: 0;
  }
  .form-inline .inputPerfil {
    vertical-align: middle;
    margin: 5px 10px 5px 0;
    padding: 8px;
    background-color: #fff;
    border: none;
    border-bottom: 2px solid #111;
  }
  .form-inline input {
    margin: 10px 0;
  }
  .form-inline .inputPerfil {
    margin: 10px 0;
    border-radius: 0;
  }
  .form-inline {
    flex-direction: column;
    align-items: stretch;
  }
  .bottom {
    display: flex;
    justify-content: center;
  }
  
  .show {
    display: block;
  }
  .hide {
    display: none;
  }
  #inputDate {
    width: 100%;
    border-radius:0;
  }
  input[type="date"]:before {
    content: attr(placeholder) !important;
  }
  /*input[type="date"]:focus:before,
  input[type="date"]:valid:before {
    content: "";
  }*/
  .mensaje {
    text-align: center;
  }
  @media (min-width: 768px) {
    .h-md-100 {
      height: 100vh;
    }
  }
  .btn-round {
    border-radius: 30px;
  }
  .bg-indigo {
    background: #111;
  }
  .text-cyan {
    color: #35bdff;
  }
  .bottom {
    display: flex;
    justify-content: center;
  }
  .btnLoginRegistro {
    width: 100%;
